.listagemtrabalhos-container{
    width: 100%;
    max-width: 1180px;
    padding: 0 30px;
    margin: 32px auto;
}

.listagemtrabalhos-container header{
    display: flex;
    align-items: center;
}

.listagemtrabalhos-container header span{
    font-size: 20px;
    margin-left: 24px;
}

.listagemtrabalhos-container header span strong{
    
    color: #128149;
}

.listagemtrabalhos-container header img{
    width: 220px;
    height: 200px;
}

.listagemtrabalhos-container header a{
    width: 260px;
    margin-left: auto;
    margin-top: 0;
}

.listagemtrabalhos-container header button{
    height: 60px;
    width: 60px;
    border-radius: 4px;
    border: 1px solid #DCDCE6;
    background: transparent;
    margin-left: 16px;
    transition: border-color 0.5s;
}

.listagemtrabalhos-container header button:hover{
    border-color: #251FC5;
}

.listagemtrabalhos-container h1{
    margin-top: 80px;
    margin-bottom: 24px;
}

.listagemtrabalhos-container ul{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 24px;
    list-style: none;
}

.listagemtrabalhos-container ul li{
    background: #ffffff;
    padding: 24px;
    border-radius: 8px;
    position: relative;
}

.listagemtrabalhos-container ul li button{
    position: absolute;
    right: 214px;
    top: 24px;
    border: 0;
    background: transparent;
}

.listagemtrabalhos-container ul li button + button{
    top: 50px;
}

.listagemtrabalhos-container ul li button:hover{
    opacity: 0.8;
}

.listagemtrabalhos-container ul li strong{
    display: block;
    margin-bottom: 16px;
    color: #41441D;
}


.listagemtrabalhos-container ul li + strong{
  margin-top: 32px;
}


.cadastro-alunocontainer ul li p{
    color: #737380;
    line-height: 21px;
    font-size: 16px;
}

.container-area-aluno{
   display: flex;
   justify-content: left;
   width: 800px;
}

.button-cad-trabalho{
    width: 180px;
    height: 40px;
    background-color: #128149;
}